<template>
	<div id="app">
		<b-navbar toggleable="md" type="dark" variant="black">
			<div class="container">
				<div>
					<div class="jo-nav-brand mr-2" :class="{ 'pulse-anim-2' : loading_story }" v-on:click="showStory()">
						<svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg" :class="{ 'anim-stroke-draw' : loading_story, 'opened' : is_story_opened}" style="enable-background:new -580 439 577.9 194;" xml:space="preserve">  
							<circle cx="50" cy="50" r="40" />
						</svg>
						<img src="./assets/jhonarendra.png" alt="Putu Jhonarendra Navbar" title="Putu Jhonarendra" class="" />
					</div>
					<b-navbar-brand to="/">
						<span class="title">Jhonarendra</span>
					</b-navbar-brand>
				</div>

				<b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

				<b-collapse id="nav-collapse" is-nav>
					<b-navbar-nav class="ml-auto">
						<b-nav-item to="/">Home</b-nav-item>
						<b-nav-item to="/about">About</b-nav-item>
						<b-nav-item to="/project">Project</b-nav-item>
						<b-nav-item to="/blog">Blog</b-nav-item>
						<b-nav-item to="/contact">Contact</b-nav-item>
					</b-navbar-nav>
				</b-collapse>
			</div>
		</b-navbar>
		<router-view 
			:key="$route.fullPath" 
			:is_explore_cd="this.is_explore_cd" 
			@setExplore="setExplore" />
		<!-- <router-view :key="$route.fullPath"/> -->
		<MobileNav />
		<Story @setStory="setStory" ref="story"/>
	</div>
</template>

<script>
import MobileNav from './components/MobileNav'
import Story from './components/Story'
export default {
	data(){
		return {
			loading_story: false,
			is_story_opened: false,
			is_explore_cd: true
		}
	},
	components: {
		MobileNav, Story
	},
	methods:{
		setExplore(val){
			this.is_explore_cd = val
		},
		getExplore(){
			return this.is_explore_cd
		},
		setStory(loading_story, is_story_opened){
			this.loading_story = loading_story
			this.is_story_opened = is_story_opened
		},
		showStory(){
			this.$refs.story.story()
		}
	},
	mounted(){
		if(localStorage.is_explore_cd){
			this.is_explore_cd = false
		} else {
			localStorage.setItem('is_explore_cd', 'true')
		}
		// this.pageview = this.updatePageview() // call di mixins
		// this.updatePageview()
	}
}
</script>