<template>
	<nav id="mobile-nav">
		<ul class="nav nav-pills nav-fill">
			<router-link to="/" class="nav-item" tag="li">
				<span class="nav-link"><small><i class="fa fa-home"></i><br />Home</small></span>
			</router-link>
			<router-link to="/about" class="nav-item" tag="li">
				<span class="nav-link"><small><i class="fa fa-user"></i><br />About</small></span>
			</router-link>
			<router-link to="/project" class="nav-item" tag="li">
				<span class="nav-link"><small><i class="fa fa-heart"></i><br />Project</small></span>
			</router-link>
			<router-link to="/blog" class="nav-item" tag="li">
				<span class="nav-link"><small><i class="fa fa-book"></i><br />Blog</small></span>
			</router-link>
			<router-link to="/contact" class="nav-item" tag="li">
				<span class="nav-link"><small><i class="fa fa-envelope"></i><br />Contact</small></span>
			</router-link>
		</ul>
	</nav>
</template>

<script>
	export default {
		data(){
			return {
				currentScrollTop: 0
			}
		},
		methods:{
			handleScroll(){
				var newScrollTop = window.scrollY
				if(this.currentScrollTop > newScrollTop){
					document.getElementById('mobile-nav').classList.remove('slideDown')
					document.getElementById('mobile-nav').classList.add('slideUp')
				} else {
					document.getElementById('mobile-nav').classList.remove('slideUp')
					document.getElementById('mobile-nav').classList.add('slideDown')
				}
				this.currentScrollTop = newScrollTop
			}
		},
		mounted(){
			window.addEventListener('scroll', this.handleScroll)
		}
	}
</script>

<style scoped="">
	nav#mobile-nav{
		display: none;
		position: fixed;
		width: 100%;
		background: rgba(0,0,0,.8);
	}
	.slideUp{
		animation: slideUp .5s;
		bottom:0;
	}
	.slideDown{
		animation: slideDown .5s;
		bottom:-80px;
	}
	@media screen and (max-width: 768px){
		nav#mobile-nav{
			display: block;
		}
	}
	@keyframes slideUp{0%{bottom:-80px}100%{bottom:0;}}
	@keyframes slideDown{0%{bottom:0}100%{bottom:-80px;}}

</style>