import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
	{path: "/", name: "Home", component: () => import("../views/Home.vue")},
	{path: "/index.html", name: "HomeIndex", component: () => import("../views/Home.vue")},
	{path: "/about", name: "About", component: () =>import("../views/About.vue")},
	{path: "/project", name: "Project", component: () =>import("../views/Project.vue")},
	{path: "/project/:slug", name: "ProjectDetail", component: () =>import("../views/ProjectDetail.vue")},
	{path: "/blog", name: "Blog", component: () =>import("../views/Blog.vue")},
	{path: "/blog/:slug", name: "BlogPost", component: () =>import("../views/BlogPost.vue")},
	{path: "/contact", name: "Contact", component: () =>import("../views/Contact.vue")}
];

const router = new VueRouter({
	mode: "history",
	base: process.env.BASE_URL,
	routes
});

export default router;
